import { polaRuangColorSet } from '@/constants';
import { useAnalysis } from '@/hooks/useAnalysis';
import {
    AlertTriangle,
    BarChart as BarChartIcon,
    Loader2,
    PieChart as PieChartIcon,
    X,
} from 'lucide-react';
import React, { useEffect, useState } from 'react';
import {
    Cell,
    Legend,
    Pie,
    PieChart,
    ResponsiveContainer,
    Tooltip,
} from 'recharts';

interface AnalysisPanelProps {
    uploadedFiles: File[];
    close: () => void;
}

const AnalysisPanel: React.FC<AnalysisPanelProps> = ({
    uploadedFiles,
    close,
}) => {
    const { runAnalysis, isAnalyzing, results, error } = useAnalysis();
    const [selectedFile, setSelectedFile] = useState<File | null>(null);

    // Auto-select first file if available
    useEffect(() => {
        if (uploadedFiles.length > 0 && !selectedFile) {
            setSelectedFile(uploadedFiles[0]);
        }
    }, [uploadedFiles, selectedFile]);

    const handleRun = () => {
        if (selectedFile) {
            runAnalysis(selectedFile);
        }
    };

    // Helper to get color
    const getColor = (name: string) => {
        const item = polaRuangColorSet.find((c) => c.namobj === name);
        return item ? item.color : '#cbd5e1'; // Default slate-300
    };

    return (
        <div className="h-full w-96 bg-slate-900/95 backdrop-blur-sm border-r border-slate-700 flex flex-col animate-in slide-in-from-left duration-300 relative z-30 shadow-2xl">
            {/* Header */}
            <div className="p-5 border-b border-slate-700 flex justify-between items-center bg-slate-900">
                <h2 className="text-lg font-bold text-primary-400 flex items-center gap-2">
                    <PieChartIcon size={20} />
                    SPATIAL ANALYSIS
                </h2>
                <button
                    onClick={close}
                    className="text-slate-500 hover:text-white transition-colors"
                >
                    <X size={20} />
                </button>
            </div>

            <div className="flex-1 overflow-y-auto p-5 space-y-6 scrollbar-thin scrollbar-thumb-slate-700">
                {/* Input Section */}
                <div className="bg-slate-800/50 rounded-lg p-4 border border-slate-700">
                    <label className="block text-xs font-bold text-slate-400 uppercase mb-2">
                        Target Polygon
                    </label>
                    {uploadedFiles.length === 0 ? (
                        <div className="text-sm text-slate-500 italic">
                            No files uploaded. Check "Import Data".
                        </div>
                    ) : (
                        <select
                            className="w-full bg-slate-900 border border-slate-700 rounded p-2 text-sm text-slate-200 focus:ring-1 focus:ring-primary-500 outline-none"
                            value={selectedFile?.name || ''}
                            onChange={(e) =>
                                setSelectedFile(
                                    uploadedFiles.find(
                                        (f) => f.name === e.target.value
                                    ) || null
                                )
                            }
                        >
                            {uploadedFiles.map((f) => (
                                <option key={f.name} value={f.name}>
                                    {f.name}
                                </option>
                            ))}
                        </select>
                    )}

                    <div className="mt-4">
                        <label className="block text-xs font-bold text-slate-400 uppercase mb-2">
                            Intersect With
                        </label>
                        <div className="flex items-center gap-2 p-2 bg-slate-900 rounded border border-slate-700 text-sm text-slate-300">
                            <div className="w-2 h-2 rounded-full bg-emerald-500" />
                            RTRW Belitung (Pola Ruang)
                        </div>
                    </div>

                    <button
                        onClick={handleRun}
                        disabled={!selectedFile || isAnalyzing}
                        className="mt-4 w-full py-2 bg-primary-600 hover:bg-primary-500 disabled:opacity-50 disabled:cursor-not-allowed text-white text-sm font-bold rounded shadow-lg transition-all flex items-center justify-center gap-2"
                    >
                        {isAnalyzing ? (
                            <>
                                <Loader2 size={16} className="animate-spin" />
                                Processing...
                            </>
                        ) : (
                            <>
                                <BarChartIcon size={16} />
                                Run Analysis
                            </>
                        )}
                    </button>
                </div>

                {/* Error State */}
                {error && (
                    <div className="bg-red-900/20 border border-red-800 p-3 rounded-lg flex gap-3 text-red-300 text-sm">
                        <AlertTriangle size={18} className="shrink-0" />
                        <p>{error}</p>
                    </div>
                )}

                {/* Results Section */}
                {results && (
                    <div className="space-y-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
                        {/* Chart */}
                        <div className="h-64 w-full">
                            <ResponsiveContainer width="100%" height="100%">
                                <PieChart>
                                    <Pie
                                        data={results}
                                        dataKey="areaHa"
                                        nameKey="category"
                                        cx="50%"
                                        cy="50%"
                                        innerRadius={60}
                                        outerRadius={80}
                                        paddingAngle={2}
                                    >
                                        {results.map((entry, index) => (
                                            <Cell
                                                key={`cell-${index}`}
                                                fill={getColor(entry.category)}
                                                stroke="rgba(0,0,0,0.2)"
                                            />
                                        ))}
                                    </Pie>
                                    <Tooltip
                                        contentStyle={{
                                            backgroundColor: '#0f172a',
                                            borderColor: '#334155',
                                            borderRadius: '0.5rem',
                                            color: '#f8fafc',
                                            fontSize: '12px',
                                        }}
                                        formatter={(value: number) =>
                                            `${value.toFixed(2)} Ha`
                                        }
                                    />
                                    <Legend
                                        verticalAlign="bottom"
                                        height={36}
                                        iconSize={8}
                                        wrapperStyle={{ fontSize: '10px' }}
                                    />
                                </PieChart>
                            </ResponsiveContainer>
                        </div>

                        {/* Breakdown Table */}
                        <div className="bg-slate-800/30 rounded-lg overflow-hidden border border-slate-700">
                            <table className="w-full text-sm text-left">
                                <thead className="text-xs uppercase bg-slate-800 text-slate-400">
                                    <tr>
                                        <th className="px-4 py-3">Zone</th>
                                        <th className="px-4 py-3 text-right">
                                            Area (Ha)
                                        </th>
                                        <th className="px-4 py-3 text-right">
                                            %
                                        </th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-slate-700">
                                    {results.map((r, i) => (
                                        <tr
                                            key={i}
                                            className="hover:bg-slate-700/30 transition-colors"
                                        >
                                            <td className="px-4 py-2 border-l-4 border-transparent flex items-center gap-2">
                                                <div
                                                    className="w-2.5 h-2.5 rounded-full shrink-0"
                                                    style={{
                                                        backgroundColor:
                                                            getColor(
                                                                r.category
                                                            ),
                                                    }}
                                                />
                                                <span
                                                    className="truncate max-w-[120px]"
                                                    title={r.category}
                                                >
                                                    {r.category}
                                                </span>
                                            </td>
                                            <td className="px-4 py-2 text-right font-mono text-slate-300">
                                                {r.areaHa.toFixed(2)}
                                            </td>
                                            <td className="px-4 py-2 text-right font-mono text-slate-300">
                                                {r.percentage.toFixed(1)}%
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default AnalysisPanel;
